import React from "react";
import { Button, Form, FormGroup, Label, Input, FormText } from "reactstrap";
import MailchimpSubscribe from "react-mailchimp-subscribe";

const CustomForm = ({ status, message, onValidated }) => {
  let email, name;
  const submit = () =>
    email &&
    name &&
    email.value.indexOf("@") > -1 &&
    onValidated({
      EMAIL: email.value,
      NAME: name.value
    });

  const submitting = status === "sending";

  return (
    <Form>
      <FormGroup>
        <Label className="sr-only">Email Address</Label>
        <Input
          ref={node => (email = node)}
          type="email"
          placeholder="joe@example.com"
        />
        <FormText color="muted">
          We won't sell your email. We hate that.
        </FormText>
      </FormGroup>
      <Button disabled={submitting} block color="primary" onClick={submit}>
        Subscribe
      </Button>
    </Form>
    // <div
    //   style={{
    //     background: "#efefef",
    //     borderRadius: 2,
    //     padding: 10,
    //     display: "inline-block"
    //   }}
    // >
    //   {status === "sending" && <div style={{ color: "blue" }}>sending...</div>}
    //   {status === "error" && (
    //     <div
    //       style={{ color: "red" }}
    //       dangerouslySetInnerHTML={{ __html: message }}
    //     />
    //   )}
    //   {status === "success" && (
    //     <div
    //       style={{ color: "green" }}
    //       dangerouslySetInnerHTML={{ __html: message }}
    //     />
    //   )}

    //   <br />
    //   <input
    //     style={{ fontSize: "2em", padding: 5 }}
    //     ref={node => (email = node)}
    //     type="email"
    //     placeholder="Your email"
    //   />
    //   <br />
    //   <button style={{ fontSize: "2em", padding: 5 }} onClick={submit}>
    //     Submit
    //   </button>
    // </div>
  );
};

export const MailChimpForm = props => {
  const url =
    "//twitter.us14.list-manage.com/subscribe/post?u=c995eaf6129ecf4b13d8ae469&id=dd2d98db0f";
  return (
    <div>
      <MailchimpSubscribe
        url={url}
        render={({ subscribe, status, message }) => (
          <CustomForm
            status={status}
            message={message}
            onValidated={formData => subscribe(formData)}
          />
        )}
      />
    </div>
  );
};
